.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .modal-content {
    width: 50%;
    max-width: 90%;
    background: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    height: 80%;
    overflow: scroll;
  }
  
  .modal-image {
    width: 200px;
    height: 200px;
    max-width: 80%;
    object-fit: cover;
  }
  